.catalog {
  @include container();



  &__wrapper {
    display: flex;
    flex-flow: row wrap;
    > div {
      &:nth-child(odd) {
        transform: translate(0, -20px); }
      &:nth-child(even) {
        transform: translate(0, 20px); } } }

  &__item {
    position: relative;
    display: flex;
    flex-flow: column;
    align-items: center;
    width: 273px;
    height: 305px;
    padding: 20px 30px;
    margin: 20px 30px;
    * {
      z-index: 2; } }

  &__item-border {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1; }

  &__item-code {
    position: absolute;
    top: 23px;
    left: 20px;
    font-size: 1.2rem;
    text-transform: uppercase;
    z-index: 3; }

  &__item-img {
    z-index: 1; }

  &__price-block {
    align-self: stretch;
    display: flex;
    flex-flow: row wrap; }

  &__item-name {
    @include resetmargin;
    @include subtitle(2.4rem);
    flex-basis: 100%;
    margin-bottom: 10px; }

  &__item-price {
    font-size: 1.8rem;
    &:after {
      content: " p."; } }

  &__item-link {
    margin-left: auto; } }
