html {
  font-size: 62.5%;
  * {
    box-sizing: border-box; } }

body {
  @include resetmargin();
  position: relative;
  font-family: "Montserrat", sans-serif;
  background-color: $bg-color; }

.btn {
  @include resetinteractive;
  display: inline-block;
  padding: 12px 10px;
  font-size: 1.2rem;
  font-family: "Montserrat", sans-serif;
  font-weight: 600;
  text-transform: uppercase;
  color: $white;
  background-color: $btn;
  cursor: pointer;
  &:hover {
    color: $btn;
    background-color: $white; }
  &--gr {
    padding: 4px 10px;
    background-size: 200% 100%;
    background-repeat: no-repeat;
    background-position: 100% 0;
    background-image: linear-gradient(90deg, #FF8BDE 50%, #669FFF 100%);
    transition: background-position $transition;
    &:hover {
      background-position: 0 0;
      color: $white; } } }

.link-btn {
  font-size: 1.6rem;
  display: inline-block;
  text-decoration: underline;
  color: $btn;
  font-weight: 600; }

.title-gradient {
  @include gbackground(100%);
  @include resetmargin;
  display: inline-block;
  padding: 0 22px;
  font-family: "Montserrat", sans-serif;
  font-size: 7.2rem;
  font-weight: 900;
  text-transform: uppercase;
  color: $white;
  @include media("<huge") {
    font-size: 6.0rem; }
  @include media("<large") {
    font-size: 4.8rem; }
  @include media("<desktop") {
    font-size: 3.6rem; }
  @include media("<tablet") {
    font-size: 2.8rem; } }


.main {
  &--padded {
    padding-top: 120px;
    @include media("<large") {
      padding-top: 100px; }
    @include media("<tablet") {
      padding-top: 90px; } } }

.icon-close {
  fill: url(#linear-grad);
  transition: fill $transition;
  &:hover {
    fill: url(#linear-grad-2); } }

.svg-gradient-line {
  stroke: url(#linear-grad); }
