.document-modal {
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  width: 100%;
  height: 100vh;
  padding: 80px 10px 10px;
  background-color: $bg-color;
  background-image: url('../img/splash-bg-1.jpg');
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  transition: opacity $transition;
  opacity: 0;
  pointer-events: none;
  * {
    pointer-events: none; }
  &__close {
    position: absolute;
    top: 50px;
    right: 70px;
    cursor: pointer; }
  &__name {
    position: absolute;
    top: 90px;
    left: 100px;
    @include subtitle(3.6rem);
    max-width: 500px;
    word-spacing: 500px; }

  &--active {
    opacity: 1;
    pointer-events: all;
    * {
      pointer-events: all; } } }
